body,
h1,
h2,
h3,
h4,
h5,
h6,
input,
textarea,
select {
  font-family: "Noto Sans KR", sans-serif;
}

body {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin: 0;
  padding: 0;
}

#root,
.app {
  min-height: 100vh;
  height: 100%;
}

a {
  text-decoration: none;
  color: #000000;
}

textarea {
  resize: none;
  border: none;
  height: calc(100% - 40px);
  width: calc(100% - 40px);
  padding: 20px;
}

textarea:focus {
  outline: none;
}

pre,
textarea,
code {
  white-space: pre-line;
}

table {
  width: auto;
  border: 1px solid #ccc;
  border-collapse: collapse;
}

td {
  border: 1px solid #ccc;
  padding: 10px;
}

thead td {
  font-weight: 600;
  font-size: 1rem;
  text-align: center;
}
